.keyboard {
    height: 46.4vh;
    width: 100%;
    margin-top: 1.6vh;
    margin-left: auto;
    margin-right: auto;
    padding: 0;
    padding-top: 3.7vw;
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(4, calc(min(calc(40vh/5),calc(100%*(4/29)))));
    column-gap: 8.7%;
    row-gap: auto;
    list-style: none;
    border-top: rgba(255, 255, 255, 0.35) solid 2px;
  }

  .row-1 {
    grid-row: 1;
  }

  .row-2 {
    grid-row: 2;
  }

  .row-3 {
    grid-row: 3;
  }

  .row-4 {
    grid-row: 4;
  }
/* 
  @media ((max-width:1079px) and (orientation: landscape)) {
    .keyboard {
      grid-template-columns: repeat(4, calc(40vh/5)) calc(40vh/5);
    }
  } */
  
  @media (min-width:481px) and (orientation: landscape) {
    .keyboard {
      grid-template-columns: repeat(4, calc(calc(100%*(4/29))));
    }
  
  }