@font-face {
  font-family: "Geometria";
  font-style: bold;
  font-weight: 700;
  src: local("Geometria"),
    url('font/geometria_bold.otf') format("opentype")
}

@font-face {
  font-family: "Geometria";
  font-style: normal;
  font-weight: 500;
  src: local("Geometria"),
    url('font/geometria_medium.otf') format("opentype")
}

.calculator-app {
  font-family: Geometria;
  color: rgba(242, 242, 242, 1);
  box-sizing: border-box;
  opacity: 0.2px;
  background: rgba(255, 255, 255, 0.2);
  font-size: 24px; 
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  padding: 10px;
  min-width: 240px;
  min-height: 320px;
}

.calculator {
  margin: 0 auto;
  box-sizing: border-box;
  padding-top: 1.6vh;
  padding-right: 3.33vw;
  padding-bottom: 4.4vh;
  padding-left: 3.19vw;
  width: 100%;
  height: 100%;
  min-width: 220px;
  min-height: 220px;
  background: linear-gradient(155.23deg, #28518E 0%, #3A77D1 100%);
  box-shadow: 0px 3.71px 7.16px 0px rgba(0, 0, 0, 0.1219),
    0px 10.27px 19.78px 0px rgba(0, 0, 0, 0.175),
    0px 24.72px 47.63px 0px rgba(0, 0, 0, 0.2281),
    0px 82px 158px 0px rgba(0, 0, 0, 0.35);
  border-radius: 18px;

}

.input-display {
  margin: 0;
  margin-top: 2.8vh;
  text-align: right;
  background: none;
  border: none;
  padding: 0;
  box-sizing: border-box;
  width: 100%;
  height: auto;
  min-height: 40px;
  font-size: 2.333em;
  line-height: 1.43;
  font-weight: 700;
  line-height: 1.43;
  overflow-x: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.expression-text::-webkit-scrollbar {
  display: none;
}

@media (min-width:481px) {
  .calculator-app {
    /* font-size: calc(8px + (16 - 8) * ((100vw - 480px) / (1440 - 480))); */
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 87.6vh;
    width: calc(0.7*87.6vh);
    border-radius: 18px;
    padding: 3.8vh 2.15vw;
    max-width: 616px;
    max-height: 876px;
  }

  .calculator {
    width: calc(min(100%, calc(0.7*100vh)));
  }

}